import React from "react";
import { useSpring, animated } from "react-spring";

const Sort = ({ handleSort, sort }) => {
  const fadeSort = useSpring({
    config: { duration: 500 },
    from: { left: -100, opacity: 0 },
    to: { opacity: 1, left: 0 }
  });
  return (
    <animated.section className="section" style={fadeSort}>
      <header>
        <h1 className="header-sort">Sort</h1>
      </header>
      <div className="sort" onChange={handleSort}>
        <div className="radio-inputs-field">
          <label htmlFor="low-to-high"> Low To High</label>
          <div className="radio-set">
            <input
              className="sort-input"
              type="radio"
              value="Low to High"
              name="sort"
              id="low-to-high"
            />
            <i className="far fa-dot-circle"></i>
          </div>
          <label htmlFor="high-to-low"> High to Low</label>
          <div className="radio-set">
            <input
              className="sort-input"
              type="radio"
              value="High to Low"
              name="sort"
              id="high-to-low"
            />
            <i className="far fa-dot-circle"></i>
          </div>
          <label htmlFor="default"> Default</label>
          <div className="radio-set">
            <input
              className="sort-input"
              type="radio"
              value="Default"
              name="sort"
              id="default"
            />
            <i className="far fa-dot-circle"></i>
          </div>
        </div>
        <button onClick={sort}>Sort</button>
      </div>
    </animated.section>
  );
};

export default Sort;
