import React, { Component } from "react";
import "./Shop.css";
import Products from "./Components/Products.js";
import Sort from "./Components/Sort.js";
import Filter from "./Components/Filter.js";
import products from "./Data/products.json";
import Nav from "./Components/nav.js";
import { BrowserRouter as Router, Route } from "react-router-dom";
import Contact from "./Components/contact.js";
import About from "./Components/about.js";
import FlipMove from "react-flip-move";

class Shop extends Component {
  constructor(props) {
    super(props);
    this.state = {
      products: products,
      direction: {
        price: ""
      },
      cart: [],
      uniqueCart: [],
      clickCart: false,
      lowPrice: "",
      highPrice: "",
      brand: false,
      brand1: false,
      brand2: false,
      brandValue: [],
      newBrand: [],
      labelUpLowPrice: false,
      labelUpHighPrice: false,
      search: "",
      searchShow: false,
      showProductsClicked: false,
      isMaxCart: false,
      isScroll: false
    };
  }

  componentDidMount() {
    window.addEventListener("scroll", () => {
      const scroll = window.scrollY < 90;
      if (scroll === false) {
        this.setState({
          isScroll: true
        });
      } else {
        this.setState({
          isScroll: false
        });
      }
    });
  }

  componentWillUnmount() {
    window.removeEventListener("scroll");
  }

  showCart = () => {
    this.setState({
      ...this.state,
      clickCart: !this.state.clickCart
    });
  };

  cancelOrder = key => {
    const deleteCart = this.state.cart.filter(c => c.id !== key);

    this.setState({ ...this.state, cart: deleteCart });
  };

  getUnique = (arr, comp) => {
    const unique = arr
      .map(e => e[comp])

      .map((e, i, final) => final.indexOf(e) === i && i)

      .filter(e => arr[e])
      .map(e => arr[e]);

    return unique;
  };

  handleSort = e => {
    this.setState({
      direction: {
        price: e.target.value
      }
    });
  };

  showProducts = () => {
    this.setState({
      ...this.state,
      showProductsClicked: !this.state.showProductsClicked
    });
  };

  sort = () => {
    if (this.state.direction.price !== "") {
      if (this.state.direction.price === "High to Low") {
        this.setState({
          products: this.state.products.sort((a, b) => b.price - a.price)
        });
      } else if (this.state.direction.price === "Low to High") {
        this.setState({
          products: this.state.products.sort((a, b) => a.price - b.price)
        });
      } else {
        this.setState({
          products: this.state.products.sort((a, b) => a.id - b.id)
        });
      }
    }
  };
  handleFiltersLowest = e => {
    this.setState({
      lowPrice: e.target.value
    });
  };

  handleFiltersHighest = e => {
    this.setState({
      highPrice: e.target.value
    });
  };

  handleBrandChecked = e => {
    this.setState({
      brand: e.target.checked
    });
  };

  handleBrandChecked1 = e => {
    this.setState({
      brand1: e.target.checked
    });
  };
  handleBrandChecked2 = e => {
    this.setState({
      brand2: e.target.checked
    });
  };

  filters = () => {
    let brand;
    let brand1;
    let brand2;

    if (this.state.brand === true) {
      brand = "AMD";
    } else {
      brand = "";
    }
    if (this.state.brand1 === true) {
      brand1 = "INTEL";
    } else {
      brand1 = "";
    }
    if (this.state.brand2 === true) {
      brand2 = "NVIDIA";
    } else {
      brand2 = "";
    }
    if (
      this.state.brand === false &&
      this.state.brand1 === false &&
      this.state.brand2 === false
    ) {
      brand = "AMD";
      brand1 = "INTEL";
      brand2 = "NVIDIA";
    }
    let filterProducts = products.filter(
      a => a.brand === brand || a.brand === brand1 || a.brand === brand2
    );
    let lowPriceFilters;
    let highPriceFilters;
    if (this.state.lowPrice !== "" || this.state.highPrice !== "") {
      if (this.state.lowPrice !== "") {
        lowPriceFilters = this.state.lowPrice;
      } else {
        lowPriceFilters = "";
      }
      if (this.state.highPrice !== "") {
        highPriceFilters = this.state.highPrice;
      } else {
        highPriceFilters = "";
      }
      filterProducts = filterProducts
        .filter(a => a.price >= lowPriceFilters)
        .filter(a => a.price <= highPriceFilters);
    }
    this.setState({
      ...this.state,
      products: filterProducts
    });
  };

  labelUp = () => {
    const lowPriceInput = document.querySelector(".low-price");

    if (lowPriceInput === document.activeElement) {
      this.setState({
        ...this.state,
        labelUpLowPrice: true
      });
    }
    if (
      lowPriceInput !== document.activeElement &&
      this.state.lowPrice === ""
    ) {
      this.setState({
        ...this.state,
        labelUpLowPrice: false
      });
    }
  };

  labelUpHighPrice = () => {
    const highPriceInput = document.querySelector(".high-price");

    if (highPriceInput === document.activeElement) {
      this.setState({
        ...this.state,
        labelUpHighPrice: true
      });
    }
    if (
      highPriceInput !== document.activeElement &&
      this.state.highPrice === ""
    ) {
      this.setState({
        ...this.state,
        labelUpHighPrice: false
      });
    }
  };

  handleSearch = e => {
    this.setState({
      ...this.state,
      search: e.target.value
    });
  };

  showSearchInput = () => {
    this.setState({
      searchShow: !this.state.searchShow
    });
  };

  render() {
    const filter = this.state.products.filter(p => {
      return p.brand.indexOf(this.state.search.toUpperCase()) !== -1;
    });
    const uniqueCart = this.getUnique(this.state.cart, "id");
    const handleAddToCart = product => {
      if (uniqueCart.length <= 2) {
        this.setState({
          ...this.state,
          cart: [...this.state.cart, product],
          isMaxCart: false
        });
      } else this.setState({ ...this.state, isMaxCart: true });
    };
    return (
      <main>
        <div id="scroll-bar"></div>
        <Router basename="/Computer-Shop/">
          <Nav
            isScroll={this.state.isScroll}
            clickCart={this.state.clickCart}
            showCart={this.showCart}
            cartProducts={this.state.uniqueCart}
            Cart={this.state.cart}
            cancelOrder={this.cancelOrder}
            showProductsClicked={this.state.showProductsClicked}
            uniqueCart={uniqueCart}
            isMaxCart={this.state.isMaxCart}
          />
          <Route
            exact
            path="/"
            render={() => {
              return (
                <div className="conteiner">
                  <h1 className="header"> Computer Shop </h1>
                  <div className="filters">
                    <Sort handleSort={this.handleSort} sort={this.sort} />
                    <Filter
                      filters={this.filters}
                      handleFiltersLowest={this.handleFiltersLowest}
                      handleFiltersHighest={this.handleFiltersHighest}
                      handleBrandChecked={this.handleBrandChecked}
                      UpadteState={this.state.brand}
                      handleCheckboxValue={this.handleCheckboxValue}
                      handleBrandChecked2={this.handleBrandChecked2}
                      labelUp={this.labelUp}
                      labelUpState={this.state.labelUpLowPrice}
                      labelUpHighState={this.state.labelUpHighPrice}
                      labelUpHighPrice={this.labelUpHighPrice}
                      handleBrandChecked1={this.handleBrandChecked1}
                      filtersPrice={this.filtersPrice}
                      handleSearch={this.handleSearch}
                      showSearchInput={this.showSearchInput}
                      searchShow={this.state.searchShow}
                    />
                  </div>
                  <section>
                    <FlipMove
                      duration={750}
                      easing="ease-in-out"
                      className="products-conteiner"
                    >
                      {filter.map(p => {
                        return (
                          <div className="wrapper" key={p.id}>
                            <Products
                              {...p}
                              addToCart={handleAddToCart}
                              cart={this.state.cart}
                            />
                          </div>
                        );
                      })}
                    </FlipMove>
                  </section>
                </div>
              );
            }}
          />
          <Route
            exact
            path="/contact"
            render={() => {
              return <Contact />;
            }}
          />
          <Route
            exact
            path="/info"
            render={() => {
              return <About />;
            }}
          />
        </Router>
        <footer>
          <p>&copy;Wszystkie prawa zastrzeżone!</p>
        </footer>
      </main>
    );
  }
}

export default Shop;
