import React from "react";
import { useSpring, animated } from "react-spring";
import logo from "./img/DEVadrian.png";

const Products = ({ addToCart, id, brand, description, price, units }) => {
  const fade = useSpring({
    config: { duration: 750 },
    from: { opacity: 0, top: -50 },
    to: { opacity: 1, top: 0 },
    delay: 500
  });
  const nameAnimate = useSpring({
    config: { duration: 500 },
    from: { top: -50 },
    to: { top: 0 },
    delay: 500
  });
  const descriptionAnimate = useSpring({
    config: { duration: 500 },
    from: { left: -100 },
    to: { left: 0 },
    delay: 500
  });

  return (
    <animated.div style={fade} className="product" id={id}>
      <animated.h1 style={nameAnimate} className="name">
        {brand}
      </animated.h1>
      <img className="products-image" src={logo} alt={brand} />
      <animated.p style={descriptionAnimate} className="description">
        {description}
      </animated.p>
      <span className="price">{`${price}zł`}</span>
      <button onClick={() => addToCart({ id, brand, price, units })}>
        Add To Cart
      </button>
    </animated.div>
  );
};
export default Products;
