import React from "react";
import brand from "./img/speed.jpg";
import { useSpring, animated } from "react-spring";

const Contact = () => {
  const animateParagraph = useSpring({
    config: { duration: 600 },
    from: { marginTop: -40, opacity: 0 },
    to: { marginTop: 20, opacity: 1 }
  });
  const animateParaghraph2 = useSpring({
    config: { duration: 400 },
    from: { marginTop: -50, opacity: 0 },
    to: { marginTop: 40, opacity: 1 },
    delay: 300
  });
  const animateContact = useSpring({
    config: { duration: 700 },
    from: { left: -400, opacity: 0 },
    to: { left: 0, opacity: 1 },
    delay: 600
  });
  const animateImg = useSpring({
    config: { duration: 600 },
    from: { marginTop: -80, opacity: 0 },
    to: { marginTop: 0, opacity: 1 }
  });

  return (
    <section className="contact-section">
      <header>
        <h1 className="contact-header">Contact</h1>
      </header>
      <h3>
        <strong>Developers High-Tech</strong>
      </h3>
      <figure>
        <animated.img
          className="brand-image"
          src={brand}
          style={animateImg}
          alt="brand"
        />
        <figcaption>Developers High-Tech</figcaption>
      </figure>
      <animated.p className="describe-contact" style={animateParagraph}>
        Lorem ipsum dolor sit amet consectetur adipisicing elit. Itaque, veniam
        iste, labore at explicabo illo sit a possimus tenetur numquam obcaecati
        accusamus officiis incidunt, quasi praesentium quaerat nisi beatae et.
        Adipisci, illo laudantium ipsa a maiores numquam! Eum dicta voluptatem,
        vero iusto consectetur aperiam necessitatibus. Voluptates sequi nam
        facilis numquam totam, cumque recusandae temporibus, ea vel impedit
        tenetur eaque esse?
      </animated.p>
      <animated.p className="describe-contact" style={animateParaghraph2}>
        Exercitationem optio culpa perspiciatis amet aliquid. Eos praesentium
        possimus a, maiores at, assumenda animi quod dolorem excepturi officiis
        aliquam, recusandae odio asperiores mollitia sint reprehenderit
        molestias aut obcaecati reiciendis numquam! Ullam numquam, beatae,
        placeat maxime quibusdam dignissimos minus doloribus voluptatum
        repudiandae voluptas magnam enim unde quos atque veniam facere.
        Necessitatibus sit sed inventore quis fugiat? Cupiditate nihil porro ab
        iusto.
      </animated.p>
      <section>
        <animated.ol className="contact" style={animateContact}>
          <a href="tel:+48-784-456-947">
            <i className="fas fa-phone-alt"></i>
            <li className="contact-list">
              <h4>Phone Number:</h4>
              <p className="contact-details">+48 784 456 947</p>
            </li>
          </a>
          <a href="https://github.com/GenWatt" target="blank">
            <i className="fab fa-github"></i>
            <li className="contact-list">
              <h4>Git-Hub:</h4>
              <p className="contact-details">GenWatt</p>
            </li>
          </a>
          <a href="https://www.facebook.com/adrian.raszka.731" target="blank">
            <i className="fab fa-facebook-square"></i>
            <li className="contact-list">
              <h4>Facebook:</h4>
              <p className="contact-details"> Adrian Raszka</p>
            </li>
          </a>
          <a
            href="https://www.linkedin.com/in/adrian-raszka-front-end/"
            target="blank"
          >
            <i className="fab fa-linkedin"></i>
            <li className="contact-list">
              <h4>LinkedIn:</h4>
              <p className="contact-details">Adrian Raszka</p>
            </li>
          </a>
        </animated.ol>
      </section>
    </section>
  );
};

export default Contact;
